ter;\n}\n\n.setup-board{\n    display: grid;\n    grid-template-columns: repeat(10, 1fr);\n    grid-template-rows: repeat(10, 1fr);\n    height: 500px;\n    width: 550px;\n}\n\n.ships-header{\n    height: 50px;\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    color: #22d3ee;\n    font-size: 1.2rem;\n}\n\n.ship-buttons{\n    display: flex;\n    gap: 10px;\n}\n\n.btn-reset{\n    background-image: url('../images/reset.svg');\n    width: 30px;\n    height: 30px;\n    background-size: cover;\n    background-position: center;\n    background-color: transparent;\n    border: none;\n}\n\n.btn-rotate {\n    width: 30px;\n    height: 30px;\n    background: none;\n    border: none;\n}\n\n.rotate-icon {\n    width: 100%;\n    height: 100%;\n    object-fit: contain;\n}\n\n.btn-randomize{\n    background-color: #22d3ee;\n    color: white;\n    border: none;\n    border-radius: 0.5rem;\n    padding: 0.4rem;\n    cursor: pointer;\n}\n\n.ship{\n    display: flex;\n    justify-content: space-between;\n    flex-grow: 1;\n    background-color: #0a1929;\n    color: #22d3ee;\n    padding: 0 1rem;\n    border-radius: 1rem;\n    cursor: pointer;\n}\n\n.ship:hover{\n    background-color: #0d2847;\n    border: 2px solid #00d4ff;\n}\n\n.ship.selected{\n    background-color: #0d2847;\n    border: 2px solid #00d4ff;\n}\n\n.ship-left{\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n}\n\n.ship-right{\n    display: flex;\n    gap: 5px;\n    align-items: end;\n    padding-bottom: 0.8rem;\n}\n\n.ship-square{\n    height: 20px;\n    width: 20px;\n    background-color: #22d3ee;\n}\n\n.ship-image{\n    width: 55px;\n    height: 55px;\n}\n\n.cell.setup{\n    border: 1px solid #1fc8e2;\n    cursor: pointer;\n}\n\n.btn-ready{\n    font-size: 1.5rem;\n    padding: 5px;\n    background-color: transparent;\n    border: none;\n    color: #444346;\n    border-bottom: #444346 5px solid;\n    border-radius: 0.5rem;\n    cursor: pointer;\n}\n\n.boards-container{\n    display: flex;\n    justify-content: center;\n    gap: 150px;\n}\n\n.left-container, .right-container{\n    display: grid;\n    height: 600px;\n    width: 650px;\n    grid-template-columns: 1fr 8fr;\n    grid-template-rows: 1fr 8fr 1fr;\n    background-color: #1a3a52;\n    padding-right: 60px;\n    border-radius: 1rem;\n    border: 2px solid #00d4ff;\n}\n\n.left-board, .right-board{\n    display: grid;\n    grid-template-columns: repeat(10, 1fr);\n    grid-template-rows: repeat(10, 1fr);\n    border: 1px solid #1fc8e2;\n}\n\n.num-labels {\n    display: flex;\n    color: #1fc8e2;\n}\n\n.num-cell {\n    display: flex;\n    flex: 1;\n    justify-content: center;\n    align-items: center;\n}\n\n.letter-labels {\n    display: flex;\n    flex-direction: column;\n    color: #1fc8e2;\n}\n\n.board-content {\n    display: flex;\n}\n\n.letter-cell {\n    display: flex;\n    flex: 1;\n    justify-content: center;\n    align-items: center;\n}\n\n.cell{\n    border: 1px solid #1fc8e2;\n}\n\n.cell.active{\n    cursor: pointer;\n}\n\n.cell.active:hover{\n    background-image: url('../images/target.svg');\n    background-size: 70% 80%;\n    background-repeat: no-repeat;\n    background-position: center;\n}\n\n.cell[data-id=\"carrier\"]{\n    background-image: url('../images/carrier.png');\n    background-size: cover;\n}\n\n.cell[data-id=\"battleship\"]{\n    background-image: url('../images/battleship.png');\n    background-size: cover;\n}\n\n.cell[data-id=\"cruiser\"]{\n    background-image: url('../images/cruiser.png');\n    background-size: cover;\n}\n\n.cell[data-id=\"submarine\"]{\n    background-image: url('../images/submarine.png');\n    background-size: cover;\n}\n\n.cell[data-id=\"destroyer\"]{\n    background-image: url('../images/destroyer.png');\n    background-size: cover;\n}\n\n.cell[data-id=\"miss\"]{\n    background-image: url('../images/x.png');\n    background-size: 60% 70%;\n    background-repeat: no-repeat;\n    background-position: center;\n}\n\n.cell[data-id=\"hit\"]{\n    background-image: url('../images/hit.png');\n    background-size: 100% 100%;\n    background-repeat: no-repeat;\n    background-position: center;\n}\n\n.left-player, .right-player{\n    display: flex;\n    align-items: center;\n    justify-content: space-between;\n    font-size: 1.2rem;\n    color: #1fc8e2;\n}\n\n.bottom{\n    font-size: 1.2rem;\n    min-height: 60px;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    background-color: #0d2642;\n    color: #22d3ee;\n}"],"sourceRoot":""}]);
